import { NextRequest, NextResponse } from 'next/server';
import { supabaseAdmin } from '@/lib/supabase-admin';

export async function POST(request: NextRequest) {
  try {
    const { id, goles_equipo, goles_rival, goleadores } = await request.json();
    if (!id) return NextResponse.json({ error: 'id requerido' }, { status: 400 });

    const auth = request.headers.get('authorization') || '';
    let token = '';
    if (auth.startsWith('Bearer ')) token = auth.split(' ')[1];
    if (!token) token = request.cookies.get('token')?.value || '';

    if (!token) {
      return NextResponse.json({ error: 'No autorizado' }, { status: 401 });
    }

    // Verificar token con Supabase
    const { data: { user }, error: authError } = await supabase.auth.getUser(token);

    if (authError || !user) {
      return NextResponse.json({ error: 'Token invÃ¡lido' }, { status: 401 });
    }

    // Verificar rol de admin
    const { data: userData, error: dbError } = await supabase
      .from('usuarios')
      .select('rol')
      .eq('id', user.id)
      .single();

    if (dbError || !userData || userData.rol !== 'admin') {
      return NextResponse.json({ error: 'No autorizado' }, { status: 401 });
    }

    // Determinar resultado
    let result: 'victoria' | 'derrota' | 'empate';
    if (goles_equipo > goles_rival) {
      result = 'victoria';
    } else if (goles_equipo < goles_rival) {
      result = 'derrota';
    } else {
      result = 'empate';
    }

    // Actualizar partido
    const update = {
      goles_equipo: goles_equipo ?? 0,
      goles_rival: goles_rival ?? 0,
      estado: 'finalizado',
      resultado: `${goles_equipo ?? 0}-${goles_rival ?? 0}`,
      result: result,
    };

    const { error: updateError } = await supabase
      .from('partidos')
      .update(update)
      .eq('id', id);

    if (updateError) {
      console.error('Error updating match:', updateError);
      return NextResponse.json({ error: 'Error interno' }, { status: 500 });
    }

    // Insertar goleadores si se proporcionan
    if (goleadores && Array.isArray(goleadores)) {
      const goalsToInsert = goleadores.map((g: { jugador_id: number; goles: number }) => ({
        partido_id: id,
        jugador_id: g.jugador_id,
        cantidad: g.goles,
      }));

      const { error: goalsError } = await supabase
        .from('goles')
        .insert(goalsToInsert);

      if (goalsError) {
        console.error('Error inserting goals:', goalsError);
        // No fallar la request por esto, pero loggear
      }
    }

    return NextResponse.json({ ok: true });
  } catch (error: any) {
    console.error('Error updating match', error.message);
    return NextResponse.json({ error: 'Error interno' }, { status: 500 });
  }
}
